.container {
  width: 100%;
  min-width: 320px;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 22px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 40px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 80px;
  }
}

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-light.woff2") format("woff2"), url("../fonts/montserrat-light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-black.woff2") format("woff2"), url("../fonts/montserrat-black.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: normal;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  min-width: 122px;
  font-weight: 700;
  font-size: 10px;
  color: #e9e9e9;
  letter-spacing: 2px;
  text-transform: uppercase;
  background-color: #ff812e;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  user-select: none;
}
.btn svg {
  color: #eae7e5;
  margin-right: 7px;
  transition: all 0.3s ease;
}
@media (min-width: 768px) {
  .btn {
    font-size: 12px;
    letter-spacing: 2.4px;
  }
  .btn svg {
    width: 14px;
    height: 14px;
  }
}
.btn:hover, .btn:focus, .btn:active {
  color: #ffffff;
  background-color: #ea5d00;
  box-shadow: 0 0 13px 0 #ff812e;
}
.btn:hover svg, .btn:focus svg, .btn:active svg {
  color: #ffffff;
}
.btn:focus-visible {
  outline: none;
}
.btn:disabled {
  background-color: #fdc6a2;
  pointer-events: none;
}
.btn:disabled span {
  display: none;
}
.btn:disabled svg {
  transform: rotate(90deg);
  margin: 0;
  color: #ffffff;
}

.new-items__text {
  display: inline-grid;
  grid-template-columns: repeat(3, auto);
  align-items: start;
  gap: 16px;
  margin-bottom: 30px;
}
.new-items__text h2 {
  font-size: 20px;
  font-weight: 900;
  margin: 0;
}

.new-items__link {
  color: #ff812e;
}

.new-items__wrapper {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.new-items__slide {
  display: flex;
  flex-direction: column;
  padding: 10px;
  transition: all 0.3s ease;
  max-width: 160px;
}
@media (min-width: 768px) {
  .new-items__slide:hover {
    box-shadow: 0 0 66px 0 #ff812e;
  }
}

.new-items__slide:not(.swiper-slide-fully-visible) {
  opacity: 0.5;
  transform: scale(0.5);
}

.new-items__additional {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2px;
}
.new-items__additional > span {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  background-color: #ff812e;
  border-radius: 25px;
  padding: 5px 10px;
  font-size: 8px;
  font-weight: 700;
  letter-spacing: 2.5px;
  text-transform: uppercase;
  color: #ffffff;
  line-height: normal;
}

.new-items__delivery {
  display: none;
}

.new-items__info {
  padding: 0 7px 10px 7px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px;
}
.new-items__info h3 {
  font-size: 14px;
  margin: 0;
  font-weight: 300;
  line-height: normal;
}
.new-items__info p {
  display: none;
}
.new-items__info > span {
  text-align: center;
  font-size: 14px;
  font-weight: 900;
}

.new-items__image-wrapper {
  margin-bottom: 11px;
}
.new-items__image-wrapper img {
  object-fit: cover;
  width: 100%;
}

.new-items__swiper-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 13.68px;
  color: #828282;
  padding: 10px;
  top: 198px;
  border: none;
  background-color: #e9e9e9;
  transition: all 0.3s ease;
  cursor: pointer;
}
.new-items__swiper-button svg {
  transition: all 0.3s ease;
}
@media (min-width: 768px) {
  .new-items__swiper-button {
    width: 50px;
    height: 47px;
    border-radius: 19px;
  }
}
.new-items__swiper-button:hover, .new-items__swiper-button:focus, .new-items__swiper-button:active {
  color: #ffffff;
  background-color: #ff812e;
}
.new-items__swiper-button:hover svg, .new-items__swiper-button:focus svg, .new-items__swiper-button:active svg {
  color: #ffffff;
}
.new-items__swiper-button:focus-visible {
  outline: none;
}
.new-items__swiper-button:disabled {
  display: none;
}

.new-items__swiper-button--prev svg {
  transform: rotate(180deg);
}

.goods h2 {
  font-size: 20px;
  font-weight: 900;
  margin: 0 0 32px 0;
}

.goods__list {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  margin: 0;
  padding: 0;
  gap: 30px 15px;
}
@media (min-width: 768px) {
  .goods__list {
    gap: 30px 3.7%;
  }
}

.goods__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2px 10px;
  color: #000000;
  min-width: 100px;
  transition: color 0.3s ease;
}
.goods__link svg {
  margin-bottom: 12px;
  transition: fill 0.3s ease;
}
.goods__link h3 {
  font-size: 11px;
  font-weight: 400;
  line-height: normal;
  margin: 0;
}
.goods__link:hover, .goods__link:focus, .goods__link:active {
  color: #ff812e;
}
.goods__link:focus-visible {
  outline: none;
}
.goods__link:disabled {
  background-color: #fdc6a2;
  pointer-events: none;
}
.goods__link:disabled span {
  display: none;
}
.goods__link:disabled svg {
  transform: rotate(90deg);
  margin: 0;
  color: #ffffff;
}